        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { addClass } from '@/utils/dom'

  import anime from 'animejs'
  export default {
    methods: {
      initAnimation() {
        const emazingAnimate = anime.timeline()
          .add({
            targets: '.letter-main',
            strokeDashoffset: {
              value: [anime.setDashoffset, 0],
              delay: 200,
              duration: 800,
              easing: 'easeInOutQuad'
            },
            stroke: {
              easing: 'easeInQuad'
            },
          })
          .add({
            targets: '.letter-e-1, .letter-a-1, .letter-i-1, .letter-g-1',
            strokeDashoffset: {
              value: [anime.setDashoffset, 0],
              duration: 400,
              easing: 'easeInOutQuad'
            },
            stroke: {
              easing: 'easeInQuad'
            },
          })
        
        emazingAnimate.play()
        emazingAnimate.complete = this.animateComplete
      },
      animateComplete() {
        addClass(this.$el.querySelector('.emazing-wrapper'), 'fade-logo')
        setTimeout(() => {
          addClass(this.$el, 'fade-out')
          setTimeout(() => {
            this.$emit('removeWelcome')
          }, 100)
        }, 250)
      }
    },
    mounted() {
      this.initAnimation()
    }
  }
</script>

<style lang="sass">
  .welcome-container
    position: fixed
    top: 0
    left: 0
    z-index: 5
    background: #FFF
    display: flex
    justify-content: center
    align-items: center
    width: 100%
    height: 100%
  .emazing-container
    position: relative
    margin: 0 auto
    width: $pageWidth
    height: 100%
  .emazing-wrapper
    position: absolute
    top: 50%
    left: 50%
    margin-top: -200px
    margin-left: -200px

    display: flex
    justify-content: center
    align-items: center
    width: 400px
    height: 400px
    .emazing
      width: 100%

  .fade-logo
    animation: fadeLogo .2s ease-in forwards
  @keyframes fadeLogo
    100%
      top: 59px
      left: 65px
      width: 120px
      height: 120px
      margin-top: -60px
      margin-left: -60px

  .fade-out
    animation: fadeOut .1s ease-in forwards
  @keyframes fadeOut
    100%
      opacity: 0
</style>



